<template>
  <v-container>
    <v-row>
      <v-col>
        <v-btn :to="{name: 'rest-api-methods-save', params: {rest_api_id: $route.params.rest_api_id}}"><v-icon>mdi-plus</v-icon> New</v-btn>
        <v-simple-table>
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">Method</th>
              <th class="text-left">Path</th>
              <th class="text-left">Function</th>
              <th class="text-left">Runtime</th>
              <th class="text-right" style="width: 100px;"></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in items" :key="item.name">
              <td>{{ item.method }}</td>
              <td>{{ item.path }}</td>
              <td>{{ item.function_name }}</td>
              <td>{{ item.function_runtime }}</td>
              <td style="text-align: right">
                <v-btn x-small :to="{name: 'rest-api-methods-save', params: {rest_api_id: $route.params.rest_api_id}, query: {id: item.id}}"><v-icon>mdi-edit</v-icon> Edit</v-btn>
              </td>
            </tr>
            <tr v-if="items.length === 0">
              <td colspan="5">No record found</td>
            </tr>
            </tbody>
          </template>
          <v-overlay :value="loading">Loading...</v-overlay>
        </v-simple-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import axios from "axios";

export default {
  data() {
    return {
      loading: false,
      items: []
    }
  },
  async mounted() {
    await this.loadItems();
  },
  methods: {
    async loadItems() {
      this.loading = true;
      try {
        let response = await axios.get('/api/rest-api-methods',{
          params: {
            project_id: this.$store.state.project.selected.id,
            rest_api_id: this.$route.params.rest_api_id
          }
        });
        this.items = response.data.rest_api_methods;
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    }
  }
}
</script>